import React, {
  ReactNode,
  FC
} from 'react';
import { css, jsx } from '@emotion/react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
}

const Button: FC<ButtonProps> = ({
  children,
  onClick
}: ButtonProps) => {
  const buttonStyles = css({
    backgroundColor: 'red',
  });
  return (
    <button
      onClick={onClick}
      css={buttonStyles}
    >
      {children}
    </button>
  )
}

export default Button;
